/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2007-05-18-standing-on-the-shoulders-of-giants-adam-keys/index.md absPath of file >>> MarkdownRemark","html":"<p>This session is totally packed. We managed to sneak in. The door guys don't like that much. If there is a fire we will all die.</p>\n<p>Adam Keys is funny and entertaining</p>\n<p>\"If you want to learn from the masters you have to read their code.\"\nRead code because \"you're surrounded by idiots.\"</p>\n<p>When you read Rails code start in the routes. You can look in the url to see where you are and then go to routes to figure out where the code is.</p>\n<p>He is giving ideas for where to start and which parts of the app you should look at.</p>\n<p>My complaint about this conference is still that the sessions aren't as advanced as I would like. I would love to see a session like we had at the Mountain West Ruby conference. Jamis Buck and Marcel Molina Jr. went through some code in front of everyone (our <a href=\"http://www.ozmozr.com\">ozmozr</a> code actually) and pointed out everything that was wrong. It was a great learning opportunity.</p>\n<p>The main point of this talk is basically that you should be taking commonly used, well developed code - plugins, rake, core Rails code etc and read through it. Seems obvious but it really does make sense.</p>","frontmatter":{"title":"Standing on the Shoulders of Giants Adam Keys","date":"May 18, 2007","imageUrl":null,"structuredHtml":null,"image":null}}},"pathContext":{"path":"/standing-on-the-shoulders-of-giants-adam-keys"}}

/***/ })

